.header-container {
   min-height: 4.375rem;
   display: flex;
   flex-direction: row;
   align-items: center;
}
.header {
   &__logo {
   }

   &__menu {
      display: flex;
      flex: 1 1 auto;
      justify-content: flex-end;
   }

   &__list {
      display: flex;
      flex-direction: row;
   }

   &__list-item {
      margin-right: 20px;
      a {
         color: $dark50;
         text-decoration: none;
         font-weight: 400;
         font-size: 14px;
         line-height: 17px;
         &:hover {
            color: $dark75;
            font-weight: 700;
            transition: 0.3s ease all;
         }
      }
   }

   &__autorization-block {
      display: flex;
      flex: 0 0 303px;
      justify-content: space-between;
   }

   &__button {
      display: inline-block;
      border-radius: 22px;
      padding: 9px 20px 10px 20px;
      font-size: 12px;
      text-decoration: none;
      text-transform: uppercase;
      font-weight: 700;
      &.header__button_transparent {
         background-color: #fff;
         border: 2px solid $purple;
         color: $purple;
      }
      &.header__button_purple {
         background: $primaryGradient;
         padding-top: 11px;
         color: #fff;
      }
   }
}
